/* ------------------------------------------
   generic.RESET
------------------------------------------ */
* {
  box-sizing: border-box;
}

img,
iframe {
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
dl, dd, ol, ul,
form, fieldset, legend,
table, th, td, caption,
hr, figure {
  margin: 0;
  padding: 0;
}

/* ------------------------------------------
   base.BODY
------------------------------------------ */
body {
  background-color: #FAFAFA;
  color: #1C1D1E;
  font-family: "Space Mono", monospace;
}

.o-wrap {
  max-width: 900px;
  margin: 0 auto;
}

body {
  background-color: black;
}

.parallax {
  position: absolute;
}

.c-hero__bg {
  background-image: url("../../images/hero-bg.jpg");
  overflow: hidden;
  background-size: cover;
  width: 100%;
  background-position: top center;
  height: 3000px;
}

.c-hero__telemark {
  background-position: top center;
  background-size: cover;
  background-image: url("../../images/hero-telemark.png");
  width: 100%;
  height: 3000px;
}

.c-hero__cables {
  background-position: top center;
  background-size: cover;
  background-image: url("../../images/hero-cables.png");
  width: 100%;
  height: 3000px;
}

.c-hero__title {
  background-image: url("../../images/hero-title.png");
  background-position: top center;
  background-size: cover;
  width: 100%;
  height: 3000px;
}

.c-content {
  padding-bottom: 100px;
  position: relative;
  top: 2300px;
  color: white;
  background-image: linear-gradient(-179deg, #152149 0%, #071029 100%);
  box-shadow: 0 -78px 94px 0 #152149;
}

.c-intro {
  margin-bottom: -200px;
  top: -250px;
  position: relative;
}

.c-intro img {
  display: block;
  margin: 0 auto;
  position: relative;
  top: 65px;
}

.c-intro iframe {
  height: 500px;
  box-shadow: 0 0 60px black;
}

.c-intro__title {
  text-align: center;
  color: #F72782;
  text-shadow: 0 0 10px #FAFAFA;
}

.c-intro__title span {
  display: inline-block;
  text-shadow: 0 0 10px #1C1D1E;
  color: #FAFAFA;
  border-bottom: 2px solid #F72782;
}

.c-lead {
  margin-bottom: 60px;
}

.c-lead p {
  font-size: 1.4375rem;
}

.c-cta {
  text-align: center;
  margin-bottom: 150px;
}

.c-button {
  text-decoration: none;
  color: #FAFAFA;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.5rem;
  padding: 15px 25px;
  background-color: #F72782;
  box-shadow: 0 0 40px #F72782;
  transition: .2s;
  display: block;
  max-width: 500px;
  margin: 0 auto;
}

.c-button:hover {
  box-shadow: 0 0 20px #F72782;
}

.c-button span {
  color: rgba(250, 250, 250, 0.9);
  font-weight: normal;
  font-size: 0.8125rem;
  display: block;
  text-transform: none;
}

.c-faq {
  margin-bottom: 100px;
}

.c-faq__title {
  font-size: 1.875rem;
  color: #F72782;
  text-transform: uppercase;
  margin-bottom: 50px;
  text-align: center;
}

.c-faq ul {
  list-style: none;
}

.c-faq__item {
  margin-bottom: 30px;
}

.c-faq__item-label {
  appearance: none;
  color: #F72782;
  border: none;
  background-color: transparent;
  display: block;
  font-weight: bold;
  cursor: pointer;
  padding: 0;
  font-size: 1.25rem;
  position: relative;
  outline: none;
}

.c-faq__item-label:before {
  content: '';
  display: block;
  border: 2px solid #F72782;
  width: 10px;
  height: 10px;
  position: absolute;
  left: -30px;
  top: 50%;
  transform: translatey(-50%);
  transition: 1s;
}

.c-faq__item-label.current:before {
  transform: translatey(-50%) rotate(45deg);
}

.c-faq__item-answer {
  display: none;
}
